* {
  box-sizing: border-box;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.navbar {
  font-size: 2rem;
  padding: 1em;
  background-color: darkgrey;
}

.navbar-logo{
  color: aqua;
  margin-right: 0.5rem;
}

.navbar-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2rem;
  background-color: burlywood;
  border-radius: 50%;
}

.add-form {
  font-size:2rem;
}

.add-input {
  margin-right: 0.2em;
  font-size:2rem;
}

.add-button {
  height: 100%;
  font-size: 1.5rem;
  padding: 0.3em 0.5em;
  background-color: green;
  color: azure;
}


.habit{
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;

}

.habit-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkgreen;
  color: white;
  border-radius: 50%;

}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover{
  opacity: 0.4;
}

.habit-increase,
.habit-decrease{
  color: goldenrod;
}

.habit-delete{
  font-size: 2rem;
  color: darkred;

}


.habits-reset {
  height: 100%;
  font-size: 1.5rem;
  padding: 0.3em 0.5em;
  background-color:brown;
  color: black;
}
